import {Card, Col, Form, OverlayTrigger, Row, Table, Tooltip } from 'react-bootstrap';
import { Debt, SavingsAtDate, useFinance } from "./FinanceContext";
import { ChangeEvent, useEffect, useState } from 'react';
import { formatCurrency, formatNumber, formatPercentage } from './FormattingHelpers';
import { Averager, DataAcquisition, OverallSavingCalculator } from './SavingsCalculators';

const DebtAnalysis = () => {
    
    const dateIndex = DataAcquisition.GetNewestDateIndex();
    const [interestPaidOnCurrentRepaymentPlan, setInterestPaidOnCurrentRepaymentPlan] = useState(0);
    
    const { dataForDates, getTotalDebtRepayment} = useFinance();
    const debts = dataForDates[dateIndex].debts;

    const [useRemaining, setUseRemaining] = useState(true);
    const [useSavingContributions, setUseSavingContributions] = useState(true);
    const [chosenRemainder, setChosenRemainder] = useState(0);
    const [chosenSavingsContributions, setChosenSavingsContributions] = useState(0);
    const initialRemainder = Averager.Remaining();
    const initialChosenSavingContribution = Averager.MonthlySavings();

    useEffect(() => 
    {
        setChosenRemainder(initialRemainder);
        setChosenSavingsContributions(initialChosenSavingContribution);
    }, [dataForDates]);

    useEffect(() => {
        let totalInterest = 0;
        debts.forEach(debt => {
            const { totalInterestPaid } = calculateTimeToPayOffEntry(debt);
            totalInterest += totalInterestPaid;
        });
        setInterestPaidOnCurrentRepaymentPlan(totalInterest);
    }, [debts]);

    const calculateTimeToPayOffEntry = (debt: Debt) =>
    {
        const monthlyInterestRate = (debt.interest / 12) / 100;
        let monthsToPayOff = 0;
        let totalInterestPaid = 0;
        let balance = debt.total;
    
        while (balance > 0)
        {          
            const interest = balance * monthlyInterestRate;
            totalInterestPaid += interest;
            balance += interest;

            if(interest > debt.monthlyRepayment)
            {
                totalInterestPaid = 9999999
                monthsToPayOff = 9999999
                break;
            }
            
            if (debt.monthlyRepayment === 0)
            {
                break;
            }

            if (balance < debt.monthlyRepayment)
            {
                balance = 0;
            }
            else 
            {
                balance -= debt.monthlyRepayment;
            }
            monthsToPayOff++;
            
            if (monthsToPayOff > 1000) 
            {
                console.log("Loop safeguard triggered. Exiting loop.");
                break;
            }
        }
            
        return {monthsToPayOff, totalInterestPaid}
    }

    const timeToPayOffEntry = (debt: Debt, index: number) =>
    {
        const {monthsToPayOff, totalInterestPaid} = calculateTimeToPayOffEntry(debt);

        return(
            <tr key={index}>
                <td>
                    {debt.name}
                </td>
                <td>
                    {monthsToPayOff.toFixed(0)}
                </td>
                <td>
                    {formatCurrency(totalInterestPaid)}
                </td>
            </tr>
        )
    }

    const timeToPayOffTable = () =>
    {
        return (
            <>
                <Table striped bordered hover className="table text-center align-middle">
                    <thead>
                        <tr>
                            <th>Item</th>
                            <th>Months Until Paid Off</th>
                            <th>Interest Paid</th>
                        </tr>
                    </thead>
                    <tbody>
                        {debts.map((debt, index) => timeToPayOffEntry(debt, index))}
                    </tbody>
                </Table>
            </>
        )
    }

    const iterateDebtRepayment = (debt: Debt): number =>
    {
        if (debt.monthlyRepayment === 0)
        {
            return 0
        }

        const monthlyInterestRate = (debt.interest / 12) / 100;
        const interest = debt.total * monthlyInterestRate 
        let balance = debt.total + interest;

        if (balance < debt.monthlyRepayment)
        {
            balance = 0.0;
        }
        else 
        {
            balance -= debt.monthlyRepayment;
        }

        debt.total = balance;
        return interest
    }
    
    const payOffDebtsForXMonths = (debts: Debt[], monthsToRun: number) =>
    {
        let totalInterestPaid = 0;
        for (let i = 0; i < monthsToRun; i++) 
        {
            let interests = debts.map((debt) => iterateDebtRepayment(debt))
            interests.forEach((interest) => {totalInterestPaid += interest;});
        }
        return totalInterestPaid;
    }
    
    const optimumRepaymentEntry = (debt: Debt, index: number, prePayDebts: Debt[]) =>
    {
        return(
            <tr key={index}>
                <td>
                    {debt.name}
                </td>
                <td>
                    {formatCurrency(prePayDebts[index].total)}
                </td>
                <td>
                    {formatPercentage(debt.interest)}
                </td>
                <td>
                    {formatCurrency(debt.monthlyRepayment)}
                </td>
                <td>
                    {formatCurrency(debt.total)}
                </td>
            </tr>
        )
    }

    const optimumRepaymentStage = (stage: number, sortedDebts: Debt[]): {totalInterestPaid: number, html: JSX.Element} =>
    {    
        const totalOverpayments = sortedDebts.reduce((total, debt) => 
        {
            if (debt.total > 0) 
            {
                return total + (debt.monthlyRepayment - debt.minimumMonthlyRepayment);
            }
            return total;
        }, 0);
        
        const completedDebtMinimumPayments = sortedDebts.reduce((total, debt) => 
        {
            if(debt.total === 0)
            {
                return total + Number(debt.minimumMonthlyRepayment);
            }
            return total;
            }, 0
        );

        let totalToOverPayWith = totalOverpayments + completedDebtMinimumPayments;
        if(useRemaining)
        {
            totalToOverPayWith += chosenRemainder
        }

        if(useSavingContributions)
        {
            totalToOverPayWith += chosenSavingsContributions;
        }

        sortedDebts.forEach(debt => 
        {
            if(debt.total === 0)
            {
                debt.monthlyRepayment = 0;
            }
            else if(totalToOverPayWith > 0)
            {
                const monthlyInterestRate = (debt.interest / 12) / 100;
                const interest = debt.total * monthlyInterestRate 
                const monthlyDebt = debt.total + interest;
                if(monthlyDebt < totalToOverPayWith)
                {
                    debt.monthlyRepayment = monthlyDebt;
                    totalToOverPayWith = totalToOverPayWith - monthlyDebt - debt.minimumMonthlyRepayment;
                }
                else
                {
                    debt.monthlyRepayment = debt.minimumMonthlyRepayment + totalToOverPayWith;
                    totalToOverPayWith = 0;
                }
            }
            else
            {
                debt.monthlyRepayment = debt.minimumMonthlyRepayment;
            }
        });

        let stageDurationInMonths = 10000;
        sortedDebts.forEach(debt =>
        {
            if(debt.total !== 0)
            {
                const {monthsToPayOff} = calculateTimeToPayOffEntry(debt);
                if (monthsToPayOff < stageDurationInMonths)
                {
                    stageDurationInMonths = monthsToPayOff;
                }
            }
        }
        )

        const prePayDebts: Debt[] = JSON.parse(JSON.stringify(sortedDebts));
        const totalInterestPaid = payOffDebtsForXMonths(sortedDebts, stageDurationInMonths);

        const html: JSX.Element = <Card key={stage}>
            <Card.Header>
                <Card.Title>
                    Stage {stage}: For {stageDurationInMonths} Months
                </Card.Title>
            </Card.Header>
            <Card.Body>
                <Table striped bordered hover className="table text-center align-middle">
                    <thead>
                        <tr>
                            <th>Item</th>
                            <th>Debt at Start Of Stage</th>
                            <th>Interest Rate</th>
                            <th>Monthly Payments</th>
                            <th>Debt at End Of Stage</th>
                        </tr>
                    </thead>
                    <tbody>
                        {sortedDebts.map((debt, index) => optimumRepaymentEntry(debt, index, prePayDebts))}
                    </tbody>
                </Table>
            </Card.Body>
        </Card>

        return {totalInterestPaid, html}
    }

    const optimumRepaymentStages = (): {totalInterestPaid: number, html: JSX.Element}[] =>
    {
        let copiedDebts: Debt[] = JSON.parse(JSON.stringify(debts));
        let sortedDebts = copiedDebts.sort((copiedDebt1, copiedDebt2) => copiedDebt2.interest - copiedDebt1.interest);
        let stageNumber = 1
        let stages: {totalInterestPaid: number, html: JSX.Element}[] = []
        
        let remainingDebt = sortedDebts.reduce((total, debt) => total + Number(debt.total), 0);
        let previousRemainingDebt = remainingDebt;
        
        while(remainingDebt > 0)
        {
            stages.push(optimumRepaymentStage(stageNumber, sortedDebts))
            remainingDebt = sortedDebts.reduce((total, debt) => total + Number(debt.total), 0);
            if(remainingDebt >= previousRemainingDebt)
            {
                break;
            }
            previousRemainingDebt = remainingDebt
            stageNumber++
        }

        return stages
    }

    const optimumRepayment = (dateIndex: number) =>
    {
        if(getTotalDebtRepayment(dateIndex) > 0)
        {
            let totalInterestPaid = 0;
            let optimumStages: JSX.Element[] = []
            const stages = optimumRepaymentStages()

            stages.forEach((stage) => {
                optimumStages.push(stage.html);
                totalInterestPaid += stage.totalInterestPaid;
            })

            return(
                <Card>
                    <Card.Header>
                        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                            <Card.Title>Target Highest Interest Strategy</Card.Title>
                            <Card.Title>Total Interest Paid: {formatCurrency(totalInterestPaid)}</Card.Title>
                        </div>
                    </Card.Header>
                    <Card.Header>
                        <Card.Title>
                            Debt Overpayment Settings
                        </Card.Title>
                        <Card.Body>
                            <Form>
                            <Table striped bordered hover className="table text-center align-middle">
                                <thead>
                                    <tr>
                                        <th>Source of Funds to overpay debt with</th>
                                        <th>Amount</th>
                                        <th>Use this to overpay</th>
                                    </tr>
                                </thead>
                                <tbody>
                                <tr>
                                    <td>
                                        <OverlayTrigger placement="top"
                                            overlay={
                                                <Tooltip>
                                                    You have some money left over every month. Do you want to use this to overpay your debts?
                                                    The initial amount is the average remaining for each entry.
                                                </Tooltip>
                                            }
                                        >
                                            <p>Remaining</p>
                                        </OverlayTrigger>
                                    </td>
                                    <td>
                                        <Form.Control
                                            type='number'
                                            onChange={(e: ChangeEvent<HTMLInputElement>) => 
                                                setChosenRemainder(parseFloat(e.target.value))
                                            }
                                            value={formatNumber(chosenRemainder)}
                                            className="mr-2" />
                                    </td>
                                    <td>
                                        <Form.Check 
                                            type="switch"
                                            checked={useRemaining}
                                            onChange={ (e:React.ChangeEvent<HTMLInputElement>) => {setUseRemaining(e.target.checked)}}/>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <OverlayTrigger placement="top"
                                                overlay={
                                                    <Tooltip>
                                                        You are saving some money. Do you want to use this to overpay your debts instead?
                                                        The initial amount is the average contributed to savings each entry.
                                                    </Tooltip>
                                                }
                                            >
                                                <p>Savings Contributions</p>
                                        </OverlayTrigger>
                                    </td>
                                    <td>
                                        <Form.Control
                                            type='number'
                                            onChange={(e: ChangeEvent<HTMLInputElement>) => 
                                                setChosenSavingsContributions(parseFloat(e.target.value))
                                            }
                                            value={formatNumber(chosenSavingsContributions)}
                                            className="mr-2" />
                                    </td>
                                    <td>
                                        <Form.Check 
                                            type="switch"
                                            checked={useSavingContributions}
                                            onChange={ (e:React.ChangeEvent<HTMLInputElement>) => {setUseSavingContributions(e.target.checked)}}/>
                                    </td>
                                </tr>
                                </tbody>
                            </Table>
                                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                </div>
                            </Form>
                        </Card.Body>
                    </Card.Header>
                    <Card.Body>
                        {optimumStages}
                    </Card.Body>
                </Card>
            )   
        }
    }

    const checkForZeroInterest = () =>
    {
        const anyWithZeroInterest = dataForDates[dateIndex].debts.some((debt) =>
        {
            return debt.interest === 0;
        });

        if(anyWithZeroInterest)
        {
            return(
                <p>Some of your debts have 0% interest. Is this an introductory offer? Does the 0% period expire? <b>Are you prepared for their expiration?</b></p>
            )
        }
        return(
            <></>
        )
    }

    const checkForSavings = () =>
    {       
        const anySavings = dataForDates[dateIndex].investments.some((investment) =>
        {
            return (investment.accessibility !== "moreThan30Days")
            && (investment.currentValue !== 0);
        });
        
        if(!anySavings)
        {
            return(<></>);
        }

        const largestInterestForDebt = debts.reduce((largestInterest: number, currentDebt) =>
        {
            if(currentDebt.interest > largestInterest)
            {
                return currentDebt.interest;
            }
            return largestInterest;
                
        }, 0);

        const savingsAtDates = dataForDates.map((dataForDate) => 
        {
            return {date: dataForDate.date, investments: dataForDate.investments}
        })
        const getOrderedCopyOfSavingsAtDates = () =>
        {
            let copiedSavingsAtDates: SavingsAtDate[] = JSON.parse(JSON.stringify(savingsAtDates));
            let sortedSavingsAtDates= copiedSavingsAtDates.sort((copiedSavingAtDate1, copiedSavingAtDate2) => 
                new Date(copiedSavingAtDate1.date).getTime() - new Date(copiedSavingAtDate2.date).getTime());
    
            return sortedSavingsAtDates;
        }
    
        const extrapolatedInterestDueToChange = new OverallSavingCalculator(getOrderedCopyOfSavingsAtDates()).ExtrapolatedAnnualInterest;                

        if(extrapolatedInterestDueToChange > largestInterestForDebt)
        {
            return(
                <p>If your savings continue on their current trend, you will earn approximately {formatPercentage(extrapolatedInterestDueToChange)} this year. Your highest interest 
                debt is charging {formatPercentage(largestInterestForDebt)} per year. This means that you are <b>earning more</b> from your savings than your highest interest debt is costing.
                <b> Are there any reasons (upcoming end of low interest period, etc) why you should use your savings to pay off this debt, despite your savings earning more than the debt is costing?</b></p>
            )
        }

        return(
            <p>If your savings continue on their current trend, you will earn approximately {formatPercentage(extrapolatedInterestDueToChange)} this year. Your highest interest 
            debt is charging {formatPercentage(largestInterestForDebt)} per year. This means that you are <b>earning less</b> from your savings than your largest debt is costing.
            <b> As your savings are earning less than the debt is costing, are there any reasons (upcoming expenses, maintaining an emergency fund, etc) why you should not use your weakest performing savings to pay off this debt?</b></p>
        )
    }

    return (
        <Card>
            <Card.Header>
            <Card.Title>
                Debt Breakdown
            </Card.Title>
            </Card.Header>
            <Card.Body>
                <Row>
                    <Col xs={9}>
                        <Card.Header>
                            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                <Card.Title>On Current Repayment Strategy</Card.Title>
                                <Card.Title>Total Interest Paid: {formatCurrency(interestPaidOnCurrentRepaymentPlan)}</Card.Title>
                            </div>
                        </Card.Header>
                        <Card.Body>
                            {timeToPayOffTable()}
                        </Card.Body>
                        <Card.Header>
                            <Card.Title>
                                In order to manage your debt, Ask yourself some Questions:
                            </Card.Title>
                        </Card.Header>
                        <Card.Body>
                            <p>Sometimes debts have costs associated with overpaying. Are there <b>additional costs associated with overpaying your debt? Is it worth overpaying despite these costs?</b> You should be able to find out about overpayment costs by reading the information provided with your debt.</p>
                            {checkForSavings()}
                            {checkForZeroInterest()}
                        </Card.Body>
                        {optimumRepayment(dateIndex)}
                    </Col>
                    <Col>
                        <Card.Header>
                            <Card.Title>
                                Considerations
                            </Card.Title>
                        </Card.Header>
                        <Card.Body>
                            <p>This debt repayment strategy prioritises paying off the debts with the highest interest. This results in minimising the overall cost of the debt.</p>
                            <p>There are many strategies to break down and manage debt. This is only one, and will not meet the needs of everyone, and as such should not be considered financial advise.</p>
                            <p>If you need financial assistance visit <a href="https://www.stepchange.org/">stepchange</a>, or reach out to a <b>registered</b> financial adviser.</p>
                            <p>If you are interested to learn more I recommend visiting <a href="https://www.moneysavingexpert.com/">moneysavingexpert</a>, or <a href="https://www.reddit.com/r/UKPersonalFinance/">Reddit's UKPersonalFinance</a> (in particular their flowchart).</p>
                        </Card.Body> 
                    </Col>
                </Row>
            </Card.Body>
        </Card>
    );
  };
  
  export default DebtAnalysis;