import { Button, Card, Col, Form, InputGroup, Row, Table } from "react-bootstrap";
import MoneyStreams, { MoneyStreamsProps } from "./MoneyStreams";
import { ChangeEvent, Component } from "react";
import { Debt, MoneyStream } from "./FinanceContext";
import InfoBox from "./InfoBox";
import DeleteIcon from '@mui/icons-material/DeleteOutline';

export interface DebtStreamsProps
{
    name: string;
    dateIndex: number;

    debts: Debt[];
    updateDebt: (dateIndex: number, index: number, updatedDebt: Debt) => void;
    addDebt: (dateIndex: number) => void;
    deleteDebt: (dateIndex: number, index: number) => void;
};

const DebtStreams = (props: DebtStreamsProps) =>
{
    const {name, debts, addDebt, dateIndex, updateDebt, deleteDebt } = props;

    const renderDebt = (debt: Debt, index: number) => {

        return (
            <tr key={index}>
                <td>
                    <Form.Control
                        type='text'
                        onChange={(e: { target: { value: string; }; }) => updateDebt(dateIndex, index, { ...debt, name: e.target.value })}
                        value={debt.name ? debt.name : ""}
                        className="mr-2" />
                </td>
                <td>
                    <Form.Control
                        type='number'
                        onChange={(e: ChangeEvent<HTMLInputElement>) => 
                            updateDebt(dateIndex,index, { ...debt, minimumMonthlyRepayment: parseFloat(e.target.value) })
                        }
                        value={debt.minimumMonthlyRepayment ? debt.minimumMonthlyRepayment : 0}
                        className="mr-2" />
                </td>
                <td>
                    <Form.Control
                        type='number'
                        onChange={(e: ChangeEvent<HTMLInputElement>) => 
                            updateDebt(dateIndex,index, { ...debt, monthlyRepayment: parseFloat(e.target.value) })
                        }
                        value={debt.monthlyRepayment ? debt.monthlyRepayment : 0}
                        className="mr-2" />
                </td>
                <td>
                    <Form.Control
                        type='number'
                        onChange={(e: ChangeEvent<HTMLInputElement>) => 
                            updateDebt(dateIndex,index, { ...debt, total: parseFloat(e.target.value) })
                        }
                        value={debt.total ? debt.total : 0}
                        className="mr-2" />
                </td>
                <td>
                    <Form.Control
                        type='number'
                        onChange={(e: ChangeEvent<HTMLInputElement>) => 
                            updateDebt(dateIndex,index, { ...debt, interest: parseFloat(e.target.value) })
                        }
                        value={debt.interest ? debt.interest : 0}
                        className="mr-2" />
                </td>
                <td>
                    <Button variant="danger" onClick={() => deleteDebt(dateIndex, index)}><DeleteIcon/></Button>
                </td>
            </tr>
        );
    };

    return (
        <>
            <Card.Header>
                <Card.Title>
                    {name}s
                </Card.Title>
            </Card.Header>
            <Card.Body>
                <Table striped bordered hover className="table text-center align-middle">
                    <thead>
                        <tr>
                            <th>Item</th>
                            <th>Minimum Monthly Repayment £ <InfoBox message="Some debts have a minimum amount that should be repaid each month. Enter that value here."/></th>
                            <th>This Months Repayment £</th>
                            <th>Total to Repay £</th>
                            <th>Annual Interest %</th>
                        </tr>
                    </thead>
                    <tbody>
                        {debts.map((debt, index: number) => renderDebt(debt, index))}
                    </tbody>
                </Table>
                <Button variant="primary" onClick={() => addDebt(dateIndex)}> Add {name} </Button>
            </Card.Body>
        </>
    );
}

export default DebtStreams