import { useState } from 'react';
import { Dropdown, OverlayTrigger, Tooltip } from 'react-bootstrap';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

function WhatIsDoneWithDataModal() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <Dropdown.Item onClick={handleShow}>
        <OverlayTrigger placement="top"
            overlay={
                <Tooltip>
                    Your Data Not Leave Your Computer
                </Tooltip>
            }
        >
            <p>What is doe with your data?</p>
        </OverlayTrigger>
      </Dropdown.Item>

      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Handling Your Data</Modal.Title>
        </Modal.Header>
        <Modal.Body><b>Your Data Not Leave This Machine</b></Modal.Body>
        <Modal.Body>When you click <b>Save</b>, the internal memory of your browser is used</Modal.Body>
        <Modal.Body>When you <b>Download</b>, you are pulling the data to the location of your browser's choosing (usually Downloads folder)</Modal.Body>
        <Modal.Footer>
          <Button variant="primary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default WhatIsDoneWithDataModal;