import { ChangeEvent } from "react";
import { DataForDate, generateUid, useFinance } from "./FinanceContext"
import { Button, Dropdown, Form } from "react-bootstrap";
import React from "react";

export const UploadFinanceData = (props: {setActiveDateId: (dateId: string) => void}) =>
{
  const setActiveDateId = props.setActiveDateId;
  const { setDataForDates, setConfig,} = useFinance();

  const uploadJson = async (event: ChangeEvent<HTMLInputElement>) => {
    console.log("Uploading Json");
    const file = event.target.files?.[0];
    if (file) {
      const text = await file.text();
      try {
        const data = JSON.parse(text);
        if (data.dataForDates && Array.isArray(data.dataForDates)) {

          const updatedDataForDates = data.dataForDates.map((item: DataForDate) => 
          {
            if(!item.uid)
            {
              item.uid = generateUid();
            }
            return item;
          })

          setDataForDates(updatedDataForDates);
          setActiveDateId(updatedDataForDates[0].uid);
        }
        if(data.config)
        {
          setConfig(data.config)
        }
      } catch (error) {
        console.error('Error parsing JSON:', error);
      }
    }
  };
  
  return   (
    <>
      <Form.Group>
        <Form.Control
          type="file"
          accept="application/json"
          onChange={uploadJson}
          style={{ display: 'none' }}
          id="file-upload"
        />
      
      <Dropdown.Item onClick={() => document.getElementById('file-upload')?.click()}>Upload</Dropdown.Item>
      </Form.Group>
    </>
  )
}

export const DownloadFinanceData: React.FC = () =>
{
    const {dataForDates, config, } = useFinance();
    
    const downloadJson = () => 
    {
        const data = {dataForDates, config}
        const json = JSON.stringify(data, null, 2); // Pretty print JSON with 2-space indentation
        const blob = new Blob([json], { type: 'application/json' });
        const url = URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        a.download = 'financeData.json';
        a.click();
        URL.revokeObjectURL(url);
    }

    return(
        <Dropdown.Item onClick={downloadJson}>Download</Dropdown.Item>
    )
};

// Function to open or create an IndexedDB database
const openDatabase = (): Promise<IDBDatabase> => {
  return new Promise((resolve, reject) => {
      const request = indexedDB.open('FinanceDatabase', 1);

      request.onupgradeneeded = (event) => {
          const db = (event.target as IDBOpenDBRequest).result;
          if (!db.objectStoreNames.contains('financeData')) {
              db.createObjectStore('financeData', { keyPath: 'id' });
          }
      };

      request.onsuccess = () => {
          resolve(request.result); // Resolve with the IDBDatabase instance
      };

      request.onerror = (event) => {
          reject(request.error); // Reject with the error from the request
      };
  });
};

export const SaveFinanceDataToDb: React.FC = () => {
  const { dataForDates, config } = useFinance();

  // Function to save the data into IndexedDB
  const saveDataToIndexedDB = async () => {
      const db = await openDatabase();

      if (db) {
          const transaction = db.transaction('financeData', 'readwrite');
          const store = transaction.objectStore('financeData');

          const data = { dataForDates, config };
          const json = JSON.stringify(data, null, 2); // Pretty print JSON with 2-space indentation
          const blob = new Blob([json], { type: 'application/json' });

          // Save blob with an id (you can choose a specific id schema, like 'finance-data')
          const item = { id: 'finance-data', blob };
          const request = store.put(item);

          request.onsuccess = () => {
              console.log('Data saved to IndexedDB');
          };

          request.onerror = () => {
              console.error('Error saving data to IndexedDB');
          };
      }
  };

  return (
      <Dropdown.Item onClick={saveDataToIndexedDB}>Save</Dropdown.Item>
  );
}

export const LoadFinanceDataFromDb = (props: { setActiveDateId: (dateId: string) => void }) => {
  const { setDataForDates, setConfig } = useFinance();
  const setActiveDateId = props.setActiveDateId;

  // Function to open or create the IndexedDB database
  const openDatabase = (): Promise<IDBDatabase> => {
    return new Promise((resolve, reject) => {
      const request = indexedDB.open('FinanceDatabase', 1);

      request.onupgradeneeded = (event) => {
        const db = (event.target as IDBOpenDBRequest).result;
        if (!db.objectStoreNames.contains('financeData')) {
          db.createObjectStore('financeData', { keyPath: 'id' });
        }
      };

      request.onsuccess = () => {
        resolve(request.result); // Resolve with the database instance
      };

      request.onerror = () => {
        reject(request.error); // Reject with the error if any
      };
    });
  };

  // Function to retrieve the data from IndexedDB
  const loadFromIndexedDB = async () => {
    try {
      const db = await openDatabase(); // Get the IndexedDB database instance
      const transaction = db.transaction('financeData', 'readonly');
      const store = transaction.objectStore('financeData');

      const request = store.get('finance-data');

      request.onsuccess = async () => {
        const result = request.result;
        if (result) {
          const blob = result.blob;
          const text = await blob.text(); // Read the blob as text
          try {
            const data = JSON.parse(text); // Parse the JSON data
            if (data.dataForDates && Array.isArray(data.dataForDates)) {
              const updatedDataForDates = data.dataForDates.map((item: DataForDate) => {
                if (!item.uid) {
                  item.uid = generateUid();
                }
                return item;
              });

              setDataForDates(updatedDataForDates);
              setActiveDateId(updatedDataForDates[0].uid); // Set the active date ID
            }
            if (data.config) {
              setConfig(data.config);
            }
          } catch (error) {
            console.error('Error parsing JSON:', error);
          }
        } else {
          console.log('No data found in IndexedDB');
        }
      };

      request.onerror = () => {
        console.error('Error retrieving data from IndexedDB');
      };
    } catch (error) {
      console.error('Failed to open database:', error);
    }
  };

  return (
    <Dropdown.Item onClick={() => loadFromIndexedDB()}>Continue From Last Save</Dropdown.Item>
  );
};

export default DownloadFinanceData;