import { Timeline, TimelineConnector, TimelineContent, TimelineDot, TimelineItem, TimelineSeparator, timelineItemClasses } from "@mui/lab"
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { Button, Card, ThemeProvider } from "react-bootstrap"
import { DataForDate, useFinance } from "./FinanceContext";
import { formatDate } from "./FormattingHelpers";
import { createTheme } from "@mui/material";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import dayjs, { Dayjs } from "dayjs";
import DeleteIcon from '@mui/icons-material/DeleteOutline';


const TimelineCard = (props: {showEditingOptions: boolean, dataForDate: DataForDate, activeDateId: string, activeDateIndex: number, setActiveDateId: (dateId: string) => void}) => 
{
    const showEditingOptions = props.showEditingOptions;
    const activeDateId = props.activeDateId;
    const activeDateIndex = props.activeDateIndex;
    const setActiveDateId = props.setActiveDateId;

    const {dataForDates, duplicateMostRecentDate, changeDateForData, deleteDataAtDate} = useFinance();

    const lightTheme = createTheme({
        palette: {
          mode: 'light',
        },
    });

    const handleDateSelection = (data: DataForDate) =>
    {
        setActiveDateId(data.uid);
    }

    const handleDateCreation = () =>
    {
        const newUid = duplicateMostRecentDate();
        setActiveDateId(newUid);
    }

    const getDotColour = (data: DataForDate) =>
    {
        if(data.uid == activeDateId)
        {
            return "primary";
        }
        return "grey";
    }
    
    const getTimelineContent = (dataForDate: DataForDate, dateIndex: number) =>
    {
        if(dataForDate.uid == activeDateId)
        {
            return(
                <ThemeProvider theme={lightTheme}>
                    <LocalizationProvider dateAdapter={AdapterDayjs}>
                        <DatePicker
                            label="Date"
                            onChange={(newValue: Dayjs | null) => {
                                const date = newValue ? newValue.toDate() : new Date();
                                changeDateForData(activeDateIndex, date);
                            }
                        }
                        value={dayjs(new Date(dataForDates[activeDateIndex].date))}
                        format="DD-MM-YYYY"     
                        />
                    </LocalizationProvider> 
                </ThemeProvider>
            );
        }
        return(
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <Button onClick={() => handleDateSelection(dataForDates[dateIndex])}>
                    {formatDate(dataForDate.date)}
                </Button>
                {showEditingOptions ? <Button variant="danger" onClick={() => deleteDataAtDate(dateIndex)}><DeleteIcon/></Button> : <></>}
            </div>
        )
    }

    return(
        <Card>
            <Card.Header>
                <Card.Title>
                    Dates
                </Card.Title>
            </Card.Header>
            <Card.Body>
                {showEditingOptions ? <Button onClick={() => handleDateCreation()}>Create Date</Button> : <></>}
                <Timeline             
                    sx={{
                        [`& .${timelineItemClasses.root}:before`]: {
                        flex: 0,
                        padding: 0,
                        },
                    }}
                >
                    {(dataForDates || []).map((dataForDate, dateIndex) => {
                        return(
                            <TimelineItem>
                                    <TimelineSeparator>
                                        <TimelineDot color={getDotColour(dataForDate)}/>
                                        <TimelineConnector />
                                    </TimelineSeparator>
                                <TimelineContent key={dateIndex} >{getTimelineContent(dataForDate, dateIndex)}</TimelineContent>
                            </TimelineItem>
                        )
                    })}
                </Timeline>
            </Card.Body>
        </Card>
    )
}

export default TimelineCard