import { SavingsAtDate, useFinance } from "./FinanceContext";

export namespace DataAcquisition
{
    export function GetNewestDateIndex() : number
    {
        const {dataForDates} = useFinance();

        const result = dataForDates.reduce((newestIndex, potentialDate, index) => 
        {
            if (!potentialDate.date || 
                (newestIndex !== null && potentialDate.date <= dataForDates[newestIndex].date)) 
            {
                return newestIndex;
            }
    
            return index;

        }, null as number | null);

        if(!result)
        {
            return 0;
        }
        return result;
    }

};

export namespace Averager
{
    function AverageInterQuartileRange(numberArray: number[]) : number
    {
        numberArray.sort((a, b) => a - b);

        const lowerQuartileIndex = Math.floor(numberArray.length * 0.25);
        const upperQuartileIndex = Math.ceil(numberArray.length * 0.75);
        numberArray = numberArray.slice(lowerQuartileIndex, upperQuartileIndex);

        const totalSavedInQuartile = numberArray.reduce((acc, val) => acc + val, 0);
        const average = totalSavedInQuartile / numberArray.length;
        return average;
    }
    
    export function MonthlySavings(): number 
    {
        const {dataForDates, getTotalSaved} = useFinance();

        let savingsArray: number[] = dataForDates.map((_, index) => getTotalSaved(index))
        return AverageInterQuartileRange(savingsArray);
    }

    export function Remaining(): number 
    {
        const {dataForDates, getTotalIncome, getTotalExpense, getTotalDebtRepayment, getTotalSaved} = useFinance();
        let remainingArray: number[] = dataForDates.map((_, index) => getTotalIncome(index) - getTotalExpense(index) - getTotalDebtRepayment(index) - getTotalSaved(index));
            
        return AverageInterQuartileRange(remainingArray);
    }
}

export class SavingsAtDateCalculator
{
    mSavingsAtDate: SavingsAtDate
    mClosestSavingsAtDate: SavingsAtDate | null

    totalValue: number;
    valueChange: number;
    changeDueToInterest: number;
    percentageChangeDueToInterest: number;
    extrapolatedInterestDueToChange: number;
    weightedRisk: number;

    constructor(savingsAtDate: SavingsAtDate, savingsAtDates: SavingsAtDate[])
    {
        this.mSavingsAtDate = savingsAtDate
        this.mClosestSavingsAtDate = this.findClosestDateBeforeSavingsAtDate(savingsAtDate, savingsAtDates)
        this.totalValue = this.calculateTotalValue();
        this.valueChange = this.calculateValueDeviation();
        this.changeDueToInterest = this.calculateDeviationDueToInterest();
        this.percentageChangeDueToInterest = this.calculatePercentageChangeDueToInterest()
        this.extrapolatedInterestDueToChange = this.calculateExtrapolatedAnnualPercentageChangeDueToInterest();

        this.weightedRisk = this.calculateWeightedRisk(savingsAtDate);
    }

    private calculateWeightedRisk(savingsAtDate: SavingsAtDate)
    {
        const totalValue = this.calculateTotalValue();

        const totalRisk = savingsAtDate.investments.reduce((totalRiskScore, saving) =>
        {
            if(saving.riskScore)
            {
                const proportionOfPortfolio = saving.currentValue / totalValue;
                const weightedRisk = proportionOfPortfolio * saving.riskScore;
                totalRiskScore += weightedRisk;
                return totalRiskScore;
            }
            return totalRiskScore;
        }, 0
        );
        return totalRisk;
    }

    private findClosestDateBeforeSavingsAtDate(savingsAtDate: SavingsAtDate, allSavingsAtDates: SavingsAtDate[])
    {
        const date = savingsAtDate.date;
        if(!date)
        {
            return null;
        }

        return allSavingsAtDates.reduce((closest, potentialSavingsAtDate) => {
            if (!potentialSavingsAtDate.date || potentialSavingsAtDate.date >= date) {
                return closest;
            }
    
            if (!closest || !closest.date || potentialSavingsAtDate.date > closest.date) {
                return potentialSavingsAtDate;
            }
    
            return closest;
        }, null as SavingsAtDate | null);
    }

    private totalValueOfSavingsAtDate(savingsAtDate: SavingsAtDate): number
    {
        return savingsAtDate.investments.reduce((total, investment) => total += investment.currentValue, 0);
    }

    private calculateTotalContributions(): number
    {
        return this.mSavingsAtDate.investments.reduce((total, investment) => total += investment.thisMonthsContribution, 0);
    }

    private calculateTotalValue(): number
    {  
        return this.totalValueOfSavingsAtDate(this.mSavingsAtDate)
    }

    private calculateValueDeviation(): number
    {
        if(!this.mClosestSavingsAtDate)
        {
            return 0
        }
        return this.calculateTotalValue() - this.totalValueOfSavingsAtDate(this.mClosestSavingsAtDate)
    }

    private calculateDeviationDueToInterest(): number
    {
        if(!this.mClosestSavingsAtDate)
        {
            return 0;
        }
        return this.calculateValueDeviation() - this.calculateTotalContributions()
    }

    private calculatePercentageChangeDueToInterest(): number
    {
        if(this.totalValue === 0)
        {
            return 0;
        }
        return (this.calculateDeviationDueToInterest() / this.totalValue) * 100
    }

    private calculateExtrapolatedAnnualPercentageChangeDueToInterest(): number
    {
        if(!this.mClosestSavingsAtDate)
        {
            return 0
        }

        const dateOfSavingsAtDate = new Date(this.mSavingsAtDate.date).getTime();
        const dateOfClosestSavingAtDate= new Date(this.mClosestSavingsAtDate.date).getTime();
        const differenceInDays = Math.ceil((dateOfSavingsAtDate - dateOfClosestSavingAtDate) / (1000 * 3600 * 24))

        return (this.calculatePercentageChangeDueToInterest() / differenceInDays) * 365
    }
}

interface SavingValues
{
    name: string;
    dates: Date[];
    values: (number | null)[];
}

export class IndividualSavingCalculator
{
    private names: string[] = [];
    SavingsValues: SavingValues[];
    ExtrapolatedInterest: SavingValues[];

    constructor(savingsAtDates: SavingsAtDate[])
    {
        this.SavingsValues = this.calculateSavingsValues(savingsAtDates);
        this.ExtrapolatedInterest = this.calculateExtrapolatedInterest(savingsAtDates);
    }

    private calculateExtrapolatedInterest(savingsAtDates: SavingsAtDate[])
    {
        const savingNames = this.getAllSavingsNames(savingsAtDates);
        let savingsValues : SavingValues[] = [];
        savingNames.forEach((savingName) =>
        {
            let savingsAtDatesCopy = JSON.parse(JSON.stringify(savingsAtDates));

            let values : (number | null)[] = [];
            let dates : Date[] = [];
            savingsAtDatesCopy.forEach((savingsAtDate: SavingsAtDate) =>
            {
                savingsAtDate.investments = savingsAtDate.investments.filter((investment) =>
                {
                    return investment.name === savingName;
                });

            });
            
            savingsAtDatesCopy.forEach((savingsAtDate: SavingsAtDate) =>
            {
                if(savingsAtDate.investments.length > 0)
                {
                    const calculator = new SavingsAtDateCalculator(savingsAtDate, savingsAtDatesCopy);
                    values.push(calculator.extrapolatedInterestDueToChange);
                }
                else
                {
                    values.push(null);
                }
                
                dates.push(savingsAtDate.date);
            });

            savingsValues.push({name: savingName, dates: dates, values: values})
        });

        return savingsValues;
    }

    private calculateSavingsValues(savingsAtDates: SavingsAtDate[])
    {
        const savingNames = this.getAllSavingsNames(savingsAtDates);

        let savingsValues : SavingValues[] = [];
        savingNames.forEach((savingName) =>
        {
            let values : (number | null)[] = [];
            let dates : Date[] = [];
            savingsAtDates.forEach((savingsAtDate: SavingsAtDate) =>
            {
                const savingFound = savingsAtDate.investments.find((investment) =>
                {
                    return investment.name === savingName;
                });

                if(savingFound)
                {
                    values.push(savingFound.currentValue)
                }
                else
                {
                    values.push(null)
                }
                dates.push(savingsAtDate.date);
            });

            savingsValues.push({name: savingName, dates: dates, values: values})
        });

        return savingsValues;
    }

    private getAllSavingsNames(savingsAtDates: SavingsAtDate[])
    {
        if(this.names.length !== 0)
        {
            return this.names;
        }

        let names: string[] = []
        savingsAtDates.forEach((savingsAtDate) =>
        {
            savingsAtDate.investments.forEach((saving) =>
            {
                if(!names.includes(saving.name))
                {
                    names.push(saving.name);
                }  
            })
        })

        this.names = names;
        return names;
    }
}

export class OverallSavingCalculator
{
    ValueDeviation: number;
    DeviationDueToInterest: number;
    PercentageInterest: number;
    ExtrapolatedAnnualInterest: number;

    constructor(savingsAtDates: SavingsAtDate[])
    {
        if(savingsAtDates.length === 0)
        {
            this.ValueDeviation = 0;
            this.DeviationDueToInterest = 0;
            this.PercentageInterest = 0;
            this.ExtrapolatedAnnualInterest = 0;
        }
        else
        {
            this.ValueDeviation = this.calculateValueDeviation(savingsAtDates);
            this.DeviationDueToInterest = this.calculateValueDeviationDueToInterest(savingsAtDates);
            this.PercentageInterest = this.calculatePercentageInterest(savingsAtDates);
            this.ExtrapolatedAnnualInterest = this.calculateExtrapolatedPercentageInterest(savingsAtDates);
        }
    }

    private calculateExtrapolatedPercentageInterest(savingsAtDates: SavingsAtDate[])
    {
        const earliestDate = this.findEarliestDate(savingsAtDates);
        const mostRecentDate = this.findMostRecentDate(savingsAtDates);
        
        const differenceInDays = Math.ceil((new Date(mostRecentDate.date).getTime() - new Date(earliestDate.date).getTime()) / (1000 * 3600 * 24))

        return (this.calculatePercentageInterest(savingsAtDates) / differenceInDays) * 365
    }

    private calculatePercentageInterest(savingsAtDates: SavingsAtDate[])
    {
        const latestDate = this.findMostRecentDate(savingsAtDates);
        const totalValueAtLatestDate = latestDate.investments.reduce((total, investment) => total += investment.currentValue, 0);
        return 100 * (this.calculateValueDeviationDueToInterest(savingsAtDates) / totalValueAtLatestDate);
    }

    private calculateValueDeviationDueToInterest(savingsAtDates: SavingsAtDate[])
    {
        const savingAtDateCalculators = savingsAtDates.map((savingsAtDate) => new SavingsAtDateCalculator(savingsAtDate, savingsAtDates));
        const totalValueDeviationDueToInterest = savingAtDateCalculators.reduce((total, calculator) => total += calculator.changeDueToInterest, 0);

        return totalValueDeviationDueToInterest;
    }

    private calculateValueDeviation(savingsAtDates: SavingsAtDate[])
    {
        const savingAtDateCalculators = savingsAtDates.map((savingsAtDate) => new SavingsAtDateCalculator(savingsAtDate, savingsAtDates));
        const totalValueDeviation = savingAtDateCalculators.reduce((total, calculator) => total += calculator.valueChange, 0);

        return totalValueDeviation
    }

    private findEarliestDate(savingsAtDate: SavingsAtDate[])
    {
        return savingsAtDate.reduce((earliest, potentialEarliest) =>
        {
            if(!earliest || earliest.date > potentialEarliest.date)
            {
                earliest = potentialEarliest;
            }
            return earliest;
        });
    }

    private findMostRecentDate(savingsAtDate: SavingsAtDate[])
    {
        return savingsAtDate.reduce((latest, potentialLatest) =>
        {
            if(!latest || latest.date < potentialLatest.date)
            {
                latest = potentialLatest;
            }
            return latest;
        });
    }

}