import {Button, Card, Col, Form, Row, Table } from 'react-bootstrap';

import { SavingsAtDate, useFinance } from "./FinanceContext";
import { LineChart, LineSeriesType } from "@mui/x-charts";
import { SavingsAtDateCalculator, IndividualSavingCalculator, OverallSavingCalculator, DataAcquisition } from "./SavingsCalculators";
import { formatCurrency, formatDate, formatPercentage } from "./FormattingHelpers";
import { useState } from 'react';

const SavingsAnalysis = () => {
    
    const dateIndex = DataAcquisition.GetNewestDateIndex();
    const { dataForDates, getTotalDebtRepayment, getTotalExpense} = useFinance();
    const savingsAtDates = dataForDates.map((dataForDate) => 
    {
        return {date: dataForDate.date, investments: dataForDate.investments}
    }
    )
    const savingCalculators = savingsAtDates.map(savingsAtDate => new SavingsAtDateCalculator(savingsAtDate, savingsAtDates));

    const getOrderedCopyOfSavingsAtDates = () =>
    {
        let copiedSavingsAtDates: SavingsAtDate[] = JSON.parse(JSON.stringify(savingsAtDates));
        let sortedSavingsAtDates= copiedSavingsAtDates.sort((copiedSavingAtDate1, copiedSavingAtDate2) => 
            new Date(copiedSavingAtDate1.date).getTime() - new Date(copiedSavingAtDate2.date).getTime());

        return sortedSavingsAtDates;
    }

    const individualSavingCalculator = new IndividualSavingCalculator(getOrderedCopyOfSavingsAtDates());
    const overallSavingCalculator = new OverallSavingCalculator(getOrderedCopyOfSavingsAtDates());

    const [showAsGraph, setShowAsGraph] = useState(true);


    const taxableVsNonTaxableLineGraph = () =>
    {
        let data: {taxable: number, taxExempt: number, date: Date}[]  = [];

        getOrderedCopyOfSavingsAtDates().forEach((savingsAtDate) => {
            if(savingsAtDate.date)
            {
                let taxableIncomeForDate = 0;
                let taxExemptIncomeForDate = 0;

                savingsAtDate.investments.forEach((saving) => {
                    saving.taxableIncome ? taxableIncomeForDate += saving.currentValue : taxExemptIncomeForDate += saving.currentValue
                })
                
                const date = new Date(savingsAtDate.date)
                data.push({taxable: taxableIncomeForDate, taxExempt: taxExemptIncomeForDate, date: date})
            }
        })

        return(
            <LineChart
                height={400}
                xAxis={[{scaleType: "time", data: data.map((datum) => datum.date)}]}
                series={[
                    {
                        data: data.map((datum) => datum.taxable), label: 'Taxable',
                        valueFormatter: (v) => (v === null? '' : formatCurrency(v))
                    },
                    {
                        data: data.map((datum) => datum.taxExempt), label: 'Tax Exempt',
                        valueFormatter: (v) => (v === null? '' : formatCurrency(v))
                    }
                ]}
            />
        )
    }

    const accessibilityLineGraph = () =>
    {
        let data: {immediate: number, upToFiveDays: number, moreThanFiveDays: number, moreThanThirtyDays: number, date: Date}[]  = [];

        getOrderedCopyOfSavingsAtDates().forEach((savingAtDate) => {
            if(savingAtDate.date)
            {
                let immediate = 0;
                let upToFiveDays = 0;
                let moreThanFiveDays = 0;
                let moreThanThirtyDays = 0;

                savingAtDate.investments.forEach((saving) => {
                    saving.accessibility === "sameDay" ? immediate += saving.currentValue 
                  : saving.accessibility === "upToFiveDays" ? upToFiveDays += saving.currentValue
                  : saving.accessibility === "moreThanFiveDays" ? moreThanFiveDays += saving.currentValue
                  : moreThanThirtyDays += saving.currentValue
                })
                
                const date = new Date(savingAtDate.date)
                data.push({immediate: immediate, upToFiveDays: upToFiveDays, moreThanFiveDays: moreThanFiveDays, moreThanThirtyDays: moreThanThirtyDays, date: date})
            }
        })

        return(
            <LineChart
                height={400}
                xAxis={[{scaleType: "time", data: data.map((datum) => datum.date)}]}
                series={[
                    {
                        data: data.map((datum) => datum.immediate), 
                        label: 'Same Day',
                        valueFormatter: (v) => (v === null? '' : formatCurrency(v))
                    },
                    {
                        data: data.map((datum) => datum.upToFiveDays), label: 'Up To 5 Business Days',
                        valueFormatter: (v) => (v === null? '' : formatCurrency(v))
                    },
                    {
                        data: data.map((datum) => datum.moreThanFiveDays), label: 'More Than 5 Business Days',
                        valueFormatter: (v) => (v === null? '' : formatCurrency(v))
                    },
                    {
                        data: data.map((datum) => datum.moreThanThirtyDays), label: 'More Than 30 Days',
                        valueFormatter: (v) => (v === null? '' : formatCurrency(v))
                    }
                ]}
            />
        )
    }
    
    const savingsTable = () =>
    {
        return(
        <Card>
            <Table striped bordered hover responsive className="table text-center align-middle">
                <thead>
                    <tr>
                        <th style={{width: '200px', textAlign: 'center'}}>
                            Date
                        </th>
                        <th style={{width: '200px', textAlign: 'center'}}>
                            Total Value
                        </th>
                        <th style={{width: '200px', textAlign: 'center'}}>
                            Value Change
                        </th>
                        <th style={{width: '200px', textAlign: 'center'}}>
                            Change Due To Interest
                        </th>
                        <th style={{width: '200px', textAlign: 'center'}}>
                            % Interest Change Since last entry
                        </th>
                        <th style={{width: '200px', textAlign: 'center'}}>
                            Extrapolated Annual % Interest Change
                        </th>
                    </tr>
                </thead>
                <tbody>
                    {savingCalculators.map((savingsAtDateCalculator, index) => 
                    {
                        return (
                            <tr key={index}>
                                <td style={{width: '200px', textAlign: 'center'}}>
                                    {savingsAtDateCalculator.mSavingsAtDate.date ? formatDate(savingsAtDateCalculator.mSavingsAtDate.date) : "Unknown"}
                                </td>
                                <td style={{width: '200px', textAlign: 'center'}}>
                                    {formatCurrency(savingsAtDateCalculator.totalValue)}
                                </td>
                                <td style={{width: '200px', textAlign: 'center'}}>
                                    {formatCurrency(savingsAtDateCalculator.valueChange)}
                                </td>
                                <td style={{width: '200px', textAlign: 'center'}}>
                                    {formatCurrency(savingsAtDateCalculator.changeDueToInterest)}
                                </td>
                                <td style={{width: '200px', textAlign: 'center'}}>
                                    {formatPercentage(savingsAtDateCalculator.percentageChangeDueToInterest)}
                                </td>
                                <td style={{width: '200px', textAlign: 'center'}}>
                                    {formatPercentage(savingsAtDateCalculator.extrapolatedInterestDueToChange)}
                                </td>
                            </tr>
                        )
                    })
                    }
                </tbody>
            </Table>
        </Card>
        )
    }

    const calculateRollingAverage = (data: number[], averageDuration: number) =>
    {
        let rollingAverage = [];
        for(let i = 0; i < data.length; i++) 
        {
            if(i === 0)
            {
                rollingAverage.push(null)
            }
            else if(i < averageDuration)
            {
                const window = data.slice(0, i + 1);
                const average = window.reduce((a: number, b: number) => a + b, 0) / window.length;
                rollingAverage.push(average)
            }
            else
            {
                const window = data.slice(i - averageDuration + 1, i + 1);
                const average = window.reduce((a: number, b: number) => a + b, 0) / averageDuration;
                rollingAverage.push(average);
            }
        }
        return rollingAverage;
    }

    const savingsLineGraph = () =>
    {
        let copiedSavingCalculators: SavingsAtDateCalculator[] = JSON.parse(JSON.stringify(savingCalculators));
        let sortedCalculators = copiedSavingCalculators.sort((copiedSavingCalculator1, copiedSavingCalculator2) => 
            new Date(copiedSavingCalculator1.mSavingsAtDate.date).getTime() - new Date(copiedSavingCalculator2.mSavingsAtDate.date).getTime());

        const rollingAverageDuration = 6;
        const rollingAverageData = calculateRollingAverage(sortedCalculators.map((datum) => datum.extrapolatedInterestDueToChange), rollingAverageDuration);
        
        return(
            <LineChart
                height={400}
                xAxis={[{scaleType: "time", data: sortedCalculators.map((datum) => new Date(datum.mSavingsAtDate.date))}]}
                yAxis={[
                    { id: 'leftAxisId' }, 
                    { id: 'rightAxisId' }]}
                series={[
                    {
                        data: sortedCalculators.map((datum) => datum.totalValue), 
                        label: 'Total Value', 
                        yAxisId: "leftAxisId",
                        valueFormatter: (v) => (v === null? '' : formatCurrency(v))
                    },
                    {
                        data: sortedCalculators.map((datum) => datum.extrapolatedInterestDueToChange), 
                        label: 'Extrapolated Annual Interest',
                        yAxisId:"rightAxisId",
                        valueFormatter: (v) => (v === null? '' : formatPercentage(v))
                    },
                    {
                        data: rollingAverageData, 
                        label: 'Rolling Average Extrapolated Annual Interest',
                        yAxisId:"rightAxisId",
                        valueFormatter: (v) => (v === null? '' : formatPercentage(v))
                    }
                ]}
                rightAxis={"rightAxisId"}
            />
        )    
    }

    const extrapolatedInterestForEachInvestment = () =>
    {
        const data = individualSavingCalculator.ExtrapolatedInterest;
        if(data.length > 0)
        {
            const seriesArray: LineSeriesType[] = data.map(datum => (
                {
                    type: "line",
                    data: datum.values,
                    label: datum.name,
                    valueFormatter: (v) => (v=== null ? '' : formatPercentage(v))
                }
            ));

            return (
                <LineChart
                    height={400}
                    xAxis={[{scaleType: "time", data: data[0].dates.map(date => new Date(date))}]}
                    series={seriesArray}
                />
            )            
        }
    }

    const getTotals = () =>
    {
        if(savingCalculators.length > 0)
        {
            return (
                <Table>
                    <thead>
                        <tr>
                            <th>
                                Value Deviation
                            </th>
                            <th>
                                Value Deviation Due to interest
                            </th>
                            <th>
                                Overall Percentage Interest
                            </th>
                            <th>
                                Extrapolated Percentage Interest
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>
                                {formatCurrency(overallSavingCalculator.ValueDeviation)}
                            </td>
                            <td>
                                {formatCurrency(overallSavingCalculator.DeviationDueToInterest)}
                            </td>
                            <td>
                                {formatPercentage(overallSavingCalculator.PercentageInterest)}
                            </td>
                            <td>
                                {formatPercentage(overallSavingCalculator.ExtrapolatedAnnualInterest)}
                            </td>
                        </tr>
                    </tbody>
                </Table>
            )
        }
    }

    return(
        <>
            <Card.Header>
                <Card.Title>
                    Savings Breakdown
                </Card.Title>
            </Card.Header>
            <Card.Body>
                {getTotals()}
            </Card.Body>
            <Card.Header>
                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                    <Card.Title>
                        Savings Over Time
                    </Card.Title>
                    <Form>
                        <Form.Check 
                            type="switch"
                            label="Show As Graph?"
                            checked={showAsGraph}
                            onChange={ (e:React.ChangeEvent<HTMLInputElement>) => {setShowAsGraph(e.target.checked)}}/>
                    </Form>
                </div>
            </Card.Header>
            <Card.Body>
                {showAsGraph ? savingsLineGraph(): savingsTable()}
            </Card.Body>
            <Card.Header>
                <Card.Title>
                    Extrapolated Interest of individual savings
                </Card.Title>
            </Card.Header>
            <Card.Body>
                {extrapolatedInterestForEachInvestment()}
            </Card.Body>
            <Card.Header>
                <Card.Title>
                    Accessibility of Savings
                </Card.Title>
            </Card.Header>
            <Card.Body>
                <p>For some individuals it is useful to have an 'emergency fund'. An emergency fund is a proportion of savings, which in the event of unexpected bills, can be accessed quickly.</p>
                <p><a href="https://www.lloydsbank.com/savings/savings-calculator/emergency-rainy-day-fund.html">Lloyds Bank</a> recommends having 3 months' of bills in an emergency fund</p>
                <p>An emergency fund with three months of expenses would be £{(getTotalExpense(dateIndex) + getTotalDebtRepayment(dateIndex)) * 3}</p>
            </Card.Body>
            <Card.Body>
                {accessibilityLineGraph()}
            </Card.Body>
            <Card.Header>
                <Card.Title>
                    Taxablility of Savings
                </Card.Title>
            </Card.Header>
            <Card.Body>
                <p>Some incomes from savings are tax free, such as ISA's or Premium bonds, but some savings are not tax free.</p>
                <p>Depending on your Income, you may have a tax free allowance.</p>
                <p>For more information about taxable savings interest vist the <a href="https://www.gov.uk/apply-tax-free-interest-on-savings">government</a> website</p>
            </Card.Body>
            <Card.Body>
                {taxableVsNonTaxableLineGraph()}
            </Card.Body>
        </>
    )
}

export default SavingsAnalysis