import React from "react"
import {Button, Card, CardBody, Col, Form, OverlayTrigger, Row, Table, Tooltip } from 'react-bootstrap';

import { DataForDate, useFinance } from "./FinanceContext";
import { LineChart, PieChart } from "@mui/x-charts";
import { formatCurrency, formatDate } from "./FormattingHelpers";
import TimelineCard from "./TimelineCard";
import { Averager } from "./SavingsCalculators"

const ExpenseAnalysis = (props: {dateIndex: number, setActiveDateId: (dateId: string) => void}) => {
    
    const dateIndex = props.dateIndex;
    const {dataForDates, getTotalExpense, getTotalIncome, getTotalDebtRepayment, getTotalSaved, config, setConfig } = useFinance();
    const activeDateId = dataForDates[dateIndex].uid;
    const expenses = dataForDates[dateIndex].expenses;
    const debts = dataForDates[dateIndex].debts;
    const savingsPerMonth = getTotalSaved(dateIndex);
    const setActiveDateId = props.setActiveDateId;

    let expenseData = expenses.filter(expense => expense.cost > 0).map(expense => ({
        label: expense.name,
        value: expense.cost,
    }));

    let debtData = debts.filter(debt => debt.monthlyRepayment > 0).map(debt => ({
        label: debt.name,
        value: debt.monthlyRepayment,
    }));

    let data = expenseData.concat(debtData)

    const remaining = getTotalIncome(dateIndex) - getTotalExpense(dateIndex) - getTotalDebtRepayment(dateIndex) - savingsPerMonth;

    if(savingsPerMonth > 0)
    {
      data = [...data, {label: "Savings", value: savingsPerMonth}]
    }

    if(remaining > 0)
    {
      data = [...data, {label: "Remaining", value: remaining}]
    }

    const getBreakdownForActiveDate = () =>
    {
      return(
        <>
        <Table striped bordered hover className="table text-center align-middle">
          <thead>
              <tr>
                  <th>Total Income</th>
                  <th>Monthly Saving Contribution</th>
                  <th>Total Monthly Expense</th>
                  <th>Total Monthly Debt Repayment</th>
                  <th>Remaining</th>
              </tr>
          </thead>
          <tbody>
            <tr>
              <td>{formatCurrency(getTotalIncome(dateIndex))}</td>
              <td>{formatCurrency(savingsPerMonth)}</td>
              <td>{formatCurrency(getTotalExpense(dateIndex))}</td>
              <td>{formatCurrency(getTotalDebtRepayment(dateIndex))}</td>
              <td>{formatCurrency(remaining)}</td>
            </tr>
          </tbody>
        </Table>
        <PieChart 
          width={600} 
          height={250} 
          series={[
            {
              data,
              highlightScope: { faded: 'global', highlighted: 'item' },
              faded: { innerRadius: 30, additionalRadius: -30, color: 'gray' },
              valueFormatter: (v) => (v === null ? '' : formatCurrency(v.value))
            },
          ]}
          />
          </>
      )
    }

    const getSavingsOverTime = () =>
    {
        const ignoreSavingOutliers = config.ignoreSavingOutliers;
        const data = JSON.parse(JSON.stringify(dataForDates.map((value: DataForDate, index: number) => ({
          date: value.date,
          amountInvested: (index === dataForDates.length - 1 && ignoreSavingOutliers) ? 0 : getTotalSaved(index)
        }))));
  
        let sortedDates = data.sort((copiedEntry1: {date: Date, amountInvested: number}, copiedEntry2: {date: Date, amountInvested: number}) => 
          new Date(copiedEntry1.date).getTime() - new Date(copiedEntry2.date).getTime());
  
        return (
          <>
            <LineChart
                    height={400}
                    xAxis={[{scaleType: "time", data: sortedDates.map((datum: {date: Date, amountInvested: number}) => new Date(datum.date))}]}
                    yAxis={[
                        { id: 'leftAxisId' }]}
                    series={[
                        {
                            data: sortedDates.map((datum: {date: Date, amountInvested: number}) => datum.amountInvested), 
                            yAxisId: "leftAxisId",
                            valueFormatter: (v) => (v === null? '' : formatCurrency(v))
                        }
                    ]}
              />
          </>
        )
    }

    const getItemOverTime = (getTotalForItemEachMonth: (dateIndex: number) => number) =>
    {
      const data = JSON.parse(JSON.stringify(dataForDates.map((value: DataForDate, index: number) => ({
        date: value.date,
        amountInvested: getTotalForItemEachMonth(index)
      }))));

      let sortedDates = data.sort((copiedEntry1: {date: Date, amountInvested: number}, copiedEntry2: {date: Date, amountInvested: number}) => 
        new Date(copiedEntry1.date).getTime() - new Date(copiedEntry2.date).getTime());

      return (
        <>
          <LineChart
                  height={400}
                  xAxis={[{scaleType: "time", data: sortedDates.map((datum: {date: Date, amountInvested: number}) => new Date(datum.date))}]}
                  yAxis={[
                      { id: 'leftAxisId' }]}
                  series={[
                      {
                          data: sortedDates.map((datum: {date: Date, amountInvested: number}) => datum.amountInvested), 
                          yAxisId: "leftAxisId",
                          valueFormatter: (v) => (v === null? '' : formatCurrency(v))
                      }
                  ]}
            />
        </>
      )
    }

    return (
      <Card>
        <Card.Header>
          <Card.Title>
            Income vs Expense Breakdown
          </Card.Title>
        </Card.Header>
        <Card.Body>
          <Row>
            <Col xs={2}>
              <TimelineCard showEditingOptions={false} dataForDate={dataForDates[dateIndex]} activeDateId={activeDateId} activeDateIndex={dateIndex} setActiveDateId={setActiveDateId} />
            </Col>
            <Col>
            <Card>
              <Card.Header>
                <Card.Title>
                  {formatDate(dataForDates[dateIndex].date)}
                </Card.Title>
              </Card.Header>
              <Card.Body>
                {getBreakdownForActiveDate()}                  
                </Card.Body>
              </Card>
              <Card>
                <Card.Header>
                  <Card.Title>
                    Over Time
                  </Card.Title>
                </Card.Header>
                <Card.Body>
                  <Card.Header>
                    <Card.Title>
                      Income
                    </Card.Title>
                  </Card.Header>
                  <Card.Body>
                    {getItemOverTime(getTotalIncome)}
                  </Card.Body>
                  <Card.Header>
                    <Card.Title>
                      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                        <p>Savings Contributions</p>
                        <p>Average {formatCurrency(Averager.MonthlySavings())}</p>
                      </div>
                    </Card.Title>
                  </Card.Header>
                  <Card.Body>
                    {getItemOverTime(getTotalSaved)}
                  </Card.Body>
                  <Card.Header>
                    <Card.Title>
                      Expenses
                    </Card.Title>
                  </Card.Header>
                  <Card.Body>
                    {getItemOverTime(getTotalExpense)}
                  </Card.Body>
                  <Card.Header>
                    <Card.Title>
                      Debt Repayments
                    </Card.Title>
                  </Card.Header>
                  <Card.Body>
                    {getItemOverTime(getTotalDebtRepayment)}
                  </Card.Body>
                </Card.Body>
              </Card>
            </Col>
          </Row>
        </Card.Body>
      </Card>
    );
  };
  
  export default ExpenseAnalysis;