import React from "react";
import { createContext, useState } from "react";

export interface MoneyStream
{
    name: string;
    cost: number;
};

export interface Debt
{
  name: string;
  monthlyRepayment: number
  minimumMonthlyRepayment: number
  total: number
  interest: number;
}

export interface Saving
{
  name: string;
  thisMonthsContribution: number;
  currentValue: number;
  accessibility: string;
  taxableIncome: boolean;
  riskScore: number;
}

export interface SavingsAtDate
{
    date: Date;
    investments: Saving[];
}

export interface SavingsGoal
{
    name: string;
    category: string;
    targetValue: number;
}

export interface Config
{
    sortSavingsAscendingOrDescending: "ascending" | "descending";
    ignoreSavingOutliers: boolean;
}

export interface DataForDate
{
    uid: string,
    date: Date;
    investments: Saving[];
    debts: Debt[];
    incomeStreams: MoneyStream[];
    expenses: MoneyStream[];
    savingGoals: SavingsGoal[];
}

interface FinanceContextType
{
    config: Config
    setConfig: (config: Config) => void;

    dataForDates: DataForDate[];
    setDataForDates: (dataForDates: DataForDate[]) => void;

    deleteDataAtDate: (dateIndex: number) => void;

    changeDateForData: (dateIndex: number, date: Date) => void;

    updateSavingsGoal: (dateIndex: number, index: number, updatedGoal: SavingsGoal) => void;
    addSavingsGoal: (dateIndex: number) => void;
    deleteSavingsGoal: (dateIndex: number, index: number) => void;

    duplicateMostRecentDate: () => string;

    updateIncome: (dateIndex: number, index: number, updatedIncome: MoneyStream) => void;
    addIncome: (dateIndex: number) => void;
    deleteIncome: (dateIndex: number, index: number) => void;

    updateExpense: (dateIndex: number, index: number, updatedExpense: MoneyStream) => void;
    addExpense: (dateIndex: number) => void;
    deleteExpense: (dateIndex: number, index: number) => void;
  
    updateDebt: (dateIndex: number, index: number, updatedDebt: Debt) => void;
    addDebt: (dateIndex: number) => void;
    deleteDebt: (dateIndex: number, index: number) => void;
    
    getTotalSaved: (dateIndex: number) => number;
    getTotalExpense: (dateIndex: number) => number;
    getTotalIncome: (dateIndex: number) => number;
    getTotalDebtRepayment: (dateIndex: number) => number;

    updateSavings: (dateIndex: number, index: number, updatedSavingsAtDate: Saving) => void;
    addSaving: (dateIndex: number) => void;
    deleteSaving: (dateIndex: number, index: number) => void;
}

const FinanceContext = createContext<FinanceContextType | undefined>(undefined);

export function generateUid(): string 
{
    return `${Date.now()}-${Math.floor(Math.random() * 10000)}`;
}

export const FinanceProvider: React.FC<React.PropsWithChildren<{}>> = ({ children }) => {

    const [config, setConfig] = useState<Config>({sortSavingsAscendingOrDescending: "descending", ignoreSavingOutliers: true})
    
    const [dataForDates, setDataForDates] = useState<DataForDate[]>([
        {
            uid: generateUid(), 
            date: new Date(), 
            debts: [{name: "Credit Card", interest: 5, minimumMonthlyRepayment: 100, monthlyRepayment: 100, total: 500}], 
            expenses: [{name: "Rent", cost:500}], 
            incomeStreams: [{name: "Work", cost: 1000}], 
            investments: [{name: "Savings Account", accessibility:"sameDay", currentValue:100, riskScore:1, taxableIncome:false, thisMonthsContribution:20}], 
            savingGoals: []
        }
    ]);

    const duplicateMostRecentDate = () : string =>
    {
        let newUid = "";
        setDataForDates(prevSavingsAtDate => {
            const mostRecentSaving = dataForDates.reduce((latest, potentialLatest) =>
            {
                if(!latest || latest.date < potentialLatest.date)
                {
                    latest = potentialLatest;
                }
                return latest;
            });

            const duplicatedItem = {
                ...mostRecentSaving,
                uid: generateUid(),
                investments: [...mostRecentSaving.investments],
                debts: [...mostRecentSaving.debts],
                incomeStreams: [...mostRecentSaving.incomeStreams],
                expenses: [...mostRecentSaving.expenses],
                savingsGoals: [...mostRecentSaving.savingGoals],
                date: new Date()
            };

            newUid = duplicatedItem.uid;

            return [
                ...prevSavingsAtDate,
                duplicatedItem
            ];
        });

        return newUid;
    }

    const deleteDataAtDate = (dateIndex: number) => {
        setDataForDates(prevDataAtDate =>
            prevDataAtDate.filter((_, parentIndex) => parentIndex !== dateIndex)
        );
    };

    const changeDateForData = (dateIndex: number, newDate: Date) =>
    {
        setDataForDates(prevDataAtDate =>
            prevDataAtDate.map((dataAtDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    return {
                        ...dataAtDate, date: newDate
                    }
                }
                else
                {
                    return dataAtDate;
                }
            }
            )
        )
    }

    const updateIncome = (dateIndex: number, index: number, updatedIncome: MoneyStream) => {
        setDataForDates(prevDataAtDate =>
            prevDataAtDate.map((dataAtDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    return {
                        ...dataAtDate, incomeStreams: dataAtDate.incomeStreams.map((income, i) => i === index ? updatedIncome : income) 
                    }
                }
                else
                {
                    return dataAtDate;
                }
            }
            )
        )
    };

    const addIncome = (dateIndex: number) => {
        
        setDataForDates(prevDataAtDate =>
            prevDataAtDate.map((dataAtDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    const newIncome: MoneyStream = { name: '', cost: 0 };
                    return {
                        ...dataAtDate, incomeStreams: [...dataAtDate.incomeStreams, newIncome]
                    }
                }
                else
                {
                    return dataAtDate;
                }
            })
        )
    };

    const deleteIncome = (dateIndex: number, index: number) => {
        setDataForDates(prevDataAtDate =>
            prevDataAtDate.map((dataForDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    return {
                        ...dataForDate, 
                        incomeStreams: dataForDate.incomeStreams.filter((_, i) => i !== index)
                    };
                }
                else
                {
                    return dataForDate;
                }
            })
        );
    };
    
    const updateSavingsGoal = (dateIndex: number, index: number, updatedSavingsGoal: SavingsGoal) => {

        setDataForDates(prevDataAtDate =>
            prevDataAtDate.map((dataAtDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    return {
                        ...dataAtDate, savingGoals: dataAtDate.savingGoals.map((savingsGoal, i) => i === index ? updatedSavingsGoal : savingsGoal) 
                    }
                }
                else
                {
                    return dataAtDate;
                }
            }
            )
        )
    };

    const addSavingsGoal = (dateIndex: number) => {
        
        setDataForDates(prevDataAtDate =>
            prevDataAtDate.map((dataAtDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    const newSavingsGoal: SavingsGoal = { name: '', category: "", targetValue: 0};
                    return {
                        ...dataAtDate, savingGoals: [...dataAtDate.savingGoals, newSavingsGoal]
                    }
                }
                else
                {
                    return dataAtDate;
                }
            })
        )
    };

    const deleteSavingsGoal = (dateIndex: number, index: number) => {
        setDataForDates(prevDataAtDate =>
            prevDataAtDate.map((dataForDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    return {
                        ...dataForDate, 
                        savingGoals: dataForDate.savingGoals.filter((_, i) => i !== index)
                    };
                }
                else
                {
                    return dataForDate;
                }
            })
        );
    };
    
    const updateExpense = (dateIndex: number, index: number, updatedExpense: MoneyStream) => {

        setDataForDates(prevDataAtDate =>
            prevDataAtDate.map((dataAtDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    return {
                        ...dataAtDate, expenses: dataAtDate.expenses.map((expense, i) => i === index ? updatedExpense : expense) 
                    }
                }
                else
                {
                    return dataAtDate;
                }
            }
            )
        )
    };

    const addExpense = (dateIndex: number) => {
        
        setDataForDates(prevDataAtDate =>
            prevDataAtDate.map((dataAtDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    const newExpense: MoneyStream = { name: '', cost: 0 };
                    return {
                        ...dataAtDate, expenses: [...dataAtDate.expenses, newExpense]
                    }
                }
                else
                {
                    return dataAtDate;
                }
            })
        )
    };

    const deleteExpense = (dateIndex: number, index: number) => {
        setDataForDates(prevDataAtDate =>
            prevDataAtDate.map((dataForDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    return {
                        ...dataForDate, 
                        expenses: dataForDate.expenses.filter((_, i) => i !== index)
                    };
                }
                else
                {
                    return dataForDate;
                }
            })
        );
    };
    
    const updateDebt = (dateIndex: number, index: number, updatedDebt: Debt) => {

        setDataForDates(prevDataAtDate =>
            prevDataAtDate.map((dataAtDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    return {
                        ...dataAtDate, debts: dataAtDate.debts.map((debt, i) => i === index ? updatedDebt : debt) 
                    }
                }
                else
                {
                    return dataAtDate;
                }
            }
            )
        )
    };

    const addDebt = (dateIndex: number) => {
        
        setDataForDates(prevDataAtDate =>
            prevDataAtDate.map((dataAtDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    const newDebt: Debt = { name: '', total: 0, interest: 0, monthlyRepayment: 0, minimumMonthlyRepayment: 0 };
                    return {
                        ...dataAtDate, debts: [...dataAtDate.debts, newDebt]
                    }
                }
                else
                {
                    return dataAtDate;
                }
            })
        )
    };

    const deleteDebt = (dateIndex: number, index: number) => {
        setDataForDates(prevDataAtDate =>
            prevDataAtDate.map((dataForDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    return {
                        ...dataForDate, 
                        debts: dataForDate.debts.filter((_, i) => i !== index)
                    };
                }
                else
                {
                    return dataForDate;
                }
            })
        );
    };

    const getTotalSaved = (dateIndex: number) => 
    {    
        return dataForDates[dateIndex].investments.reduce((total, investment) => total + Number(investment.thisMonthsContribution), 0);
    }

    const getTotalExpense = (dateIndex: number) => 
    {    
        return dataForDates[dateIndex].expenses.reduce((total, expense) => total + Number(expense.cost), 0);
    }

    const getTotalIncome = (dateIndex: number) => 
    {    
        return dataForDates[dateIndex].incomeStreams.reduce((total, income) => total + Number(income.cost), 0);
    }  

    const getTotalDebtRepayment = (dateIndex: number) => 
    {    
        return dataForDates[dateIndex].debts.reduce((total, debt) => total + Number(debt.monthlyRepayment), 0);
    }

    const updateSavings = (dateIndex: number, index: number, updatedSaving: Saving) => {
        setDataForDates(prevDataAtDate =>
            prevDataAtDate.map((dataAtDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    return {
                        ...dataAtDate, investments: dataAtDate.investments.map((saving, i) => i === index ? updatedSaving : saving) 
                    }
                }
                else
                {
                    return dataAtDate;
                }
            }
            )
        )
    };

    const addSaving = (dateIndex: number) => 
    {
        setDataForDates(prevDataAtDate =>
            prevDataAtDate.map((dataAtDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    const newSaving: Saving = {name: "", thisMonthsContribution: 0, currentValue:0, accessibility: "sameDay", taxableIncome: false, riskScore: 1}
                    return {
                        ...dataAtDate, investments: [...dataAtDate.investments, newSaving]
                    }
                }
                else
                {
                    return dataAtDate;
                }
            })
        )
    };

    const deleteSaving = (dateIndex: number, index: number) => 
    {
        setDataForDates(prevDataAtDate =>
            prevDataAtDate.map((dataForDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    return {
                        ...dataForDate, 
                        investments: dataForDate.investments.filter((_, i) => i !== index)
                    };
                }
                else
                {
                    return dataForDate;
                }
            })
        );
    };
  
  return (
    <FinanceContext.Provider value={{   config, setConfig, changeDateForData, deleteDataAtDate,
                                        dataForDates, setDataForDates, duplicateMostRecentDate,
                                        updateExpense, addExpense, deleteExpense, 
                                        updateIncome, addIncome, deleteIncome,
                                        updateSavingsGoal, addSavingsGoal, deleteSavingsGoal,
                                        updateDebt, addDebt, deleteDebt,
                                        updateSavings, addSaving, deleteSaving,
                                        getTotalExpense, getTotalIncome, getTotalDebtRepayment, getTotalSaved
                                     }}>
      {children}
    </FinanceContext.Provider>
  );
};

export const useFinance = () => {
  const context = React.useContext(FinanceContext);
  if (!context) {
      throw new Error('useFinance must be used within a FinanceProvider');
  }
  return context;
};