import { Accordion, Button, Card, Form, InputGroup, OverlayTrigger, Table, Tooltip } from "react-bootstrap";
import { Saving, SavingsAtDate, useFinance } from "./FinanceContext";
import { ChangeEvent } from "react";
import { formatNumber } from "./FormattingHelpers";
import { SavingsAtDateCalculator } from "./SavingsCalculators";
import DeleteIcon from '@mui/icons-material/DeleteOutline';
import InfoBox from "./InfoBox";

const InputSavingsData: React.FC<{dateIndex: number}> = (props: {dateIndex: number}) =>
{
    const dateIndex = props.dateIndex;
    const {dataForDates, updateSavings, deleteSaving, addSaving} = useFinance();
    
    const savingsAtDates = dataForDates.map((dataForDate) =>
    {
        return ({date: dataForDate.date, investments: dataForDate.investments})
    })

    const savingsAtDate = {date: dataForDates[dateIndex].date, investments: dataForDates[dateIndex].investments}

    const renderSaving = (saving: Saving, index: number, parentIndex: number) =>
    {
        return(
            <tr key={index}>
                <td>
                    <Form.Control
                        type='text'
                        onChange={(e: { target: { value: string; }; }) => updateSavings(parentIndex, index, { ...saving, name: e.target.value })}
                        value={saving.name ? saving.name : ""}
                        className="mr-2" />
                </td>
                <td>
                    <Form.Control
                        type='number'
                        onChange={(e: ChangeEvent<HTMLInputElement>) => 
                            updateSavings(parentIndex, index, { ...saving, thisMonthsContribution: parseFloat(e.target.value) })
                        }
                        value={saving.thisMonthsContribution ? saving.thisMonthsContribution : 0}
                        className="mr-2" />
                </td>
                <td>
                    <Form.Control
                        type='number'
                        onChange={(e: ChangeEvent<HTMLInputElement>) => 
                            updateSavings(parentIndex, index, { ...saving, currentValue: parseFloat(e.target.value) })
                        }
                        value={saving.currentValue ? saving.currentValue : 0}
                        className="mr-2" />
                </td>
                <td>
                    <Form.Select
                        onChange={(e: ChangeEvent<HTMLSelectElement>) => 
                            updateSavings(parentIndex, index, { ...saving, accessibility: e.target.value })
                        }
                        value={saving.accessibility ? saving.accessibility : "immediate"}
                    >
                        <option value = "sameDay"> Immediate </option>
                        <option value = "upToFiveDays"> Up To Five business Days </option>
                        <option value = "moreThanFiveDays"> More Than Five Days </option>
                        <option value = "moreThan30Days"> More Than Thirty Days </option>
                    </Form.Select>
                </td>
                <td>
                    <InputGroup className="mb-3 mt-2">
                        <Form.Check
                            type="switch"
                            onChange={(e: ChangeEvent<HTMLInputElement>) => 
                                updateSavings(parentIndex, index, { ...saving, taxableIncome:(e.target.checked) })
                            }
                            checked={saving.taxableIncome}
                            className="mt-3;"
                            />
                    </InputGroup>
                </td>
                <td>
                    <OverlayTrigger placement="top"
                                    overlay={
                                        <Tooltip>
                                            How risky is this investment? 1 being not at all, 100 being extremely
                                        </Tooltip>
                                    }
                    >
                        <InputGroup className="mb-3 mt-2">
                            <Form.Control
                                type='number'
                                min="1"
                                max="100"
                                onChange={(e: ChangeEvent<HTMLInputElement>) => 
                                    updateSavings(parentIndex, index, { ...saving, riskScore: parseFloat(e.target.value) })
                                }
                                value={saving.riskScore ? saving.riskScore : 0}
                                className="mr-2" />
                        </InputGroup>
                    </OverlayTrigger>
                </td>
                <td>
                    <Button variant="danger" onClick={() => deleteSaving(parentIndex, index)}><DeleteIcon/></Button>
                </td>
            </tr>
        )
    }

    const SavingsForDate = (savingsAtDate: SavingsAtDate) =>
    {
        return(
        <>
            <Table striped bordered hover className="table text-center align-middle">
                <thead>
                    <tr>
                        <th>
                            Name
                        </th>
                        <th>
                            Transfers To/From Investment <InfoBox message="Have you added to or removed any funds from a saving. Enter that value here (negative for withdrawals)"/>
                        </th>
                        <th>
                            Current Value
                        </th>
                        <th>
                            Accessibility
                        </th>
                        <th>
                            Taxable Income
                        </th>
                        <th>
                            Risk Score
                        </th>
                    </tr>
                </thead>
                <tbody>
                    {savingsAtDate.investments.map((saving, index: number) => renderSaving(saving, index, dateIndex))}
                </tbody>
            </Table>
            <Button variant="primary" onClick={() => addSaving(dateIndex)}>Add Saving</Button>
        </>
        )
    }

    const getWeightedRisk = (dateIndex: number) =>
    {
        const savingsAtDateCalculator = new SavingsAtDateCalculator(savingsAtDates[dateIndex], savingsAtDates)
        return (<>Weighted Risk: {formatNumber(savingsAtDateCalculator.weightedRisk)}</>)
    }

    return(
       <>
        <Card.Header>
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <Card.Title>
                    Savings/Investments
                </Card.Title>
            </div>
        </Card.Header>
        <Card.Footer>
        </Card.Footer>
        <Card.Body>
            <Accordion>
                {SavingsForDate(savingsAtDate)}
            </Accordion>
        </Card.Body>
      </>
    )
  };
  
  export default InputSavingsData;
  