import { ChangeEvent, Component } from "react";
import { Button, Card, Form, Table } from "react-bootstrap";
import { MoneyStream } from "./FinanceContext";
import { formatCurrency } from "./FormattingHelpers";
import DeleteIcon from '@mui/icons-material/DeleteOutline';

export interface MoneyStreamsProps
{
    name: string;

    dateIndex: number;
    streams: MoneyStream[];
    updateStream: (dateIndex: number, index: number, updatedExpense: MoneyStream) => void;
    addStream: (dateIndex: number) => void;
    deleteStream: (dateIndex: number, index: number) => void;
};

const MoneyStreams = (props: MoneyStreamsProps) =>
{
    const { dateIndex, name, streams, addStream } = props;
    
    const renderStream = (expense: { name: string; cost: number; }, index: number) => {
        const { updateStream, deleteStream } = props;

        return (
            <tr key={index}>
                <td>
                    <Form.Control
                        type='text'
                        onChange={(e: { target: { value: string; }; }) => updateStream(dateIndex, index, { ...expense, name: e.target.value })}
                        value={expense.name ? expense.name : ""}
                        className="mr-2" />
                </td>
                <td>
                    <Form.Control
                        type='number'
                        onChange={(e: ChangeEvent<HTMLInputElement>) => 
                            updateStream(dateIndex, index, { ...expense, cost: parseFloat(e.target.value) })
                        }
                        value={expense.cost ? expense.cost : 0}
                        className="mr-2" />
                </td>
                <td>
                    <p>{formatCurrency(expense.cost * 12)}</p>
                </td>
                <td>
                    <Button variant="danger" onClick={() => deleteStream(dateIndex, index)}><DeleteIcon/></Button>
                </td>
            </tr>
        );
    };

    return (
        <>
            <Card.Header>
                <Card.Title>
                    {name}s
                </Card.Title>
            </Card.Header>
            <Card.Body>
                <Table striped bordered hover className="table text-center align-middle">
                    <thead>
                        <tr>
                            <th>Item</th>
                            <th>Monthly £</th>
                            <th>Annual £</th>
                        </tr>
                    </thead>
                    <tbody>
                        {streams.map((stream, index: number) => renderStream(stream, index))}
                    </tbody>
                </Table>
                <Button variant="primary" onClick={() => addStream(dateIndex)}>
                    Add {name}
                </Button>
            </Card.Body>
        </>
    );
}
export default MoneyStreams